.map-wrapper {
  position: relative;
  height: 0;
  width: 100%;
  overflow: hidden;
  padding-top: 80%;
  border: 1px solid #d6d7d9; }
  .map-wrapper.map-loading:before {
    z-index: 1039; }
  .map-wrapper.map-loading.map-style-fullscreen .fullscreen-wrap:before {
    z-index: 2; }
  .map-wrapper.map-loading.map-style-fullscreen .fullscreen-wrap:after {
    content: "\f110";
    color: rgba(0, 0, 0, 0.5);
    font-size: 30px;
    line-height: 30px;
    -webkit-animation: fa-spin 1s infinite steps(8);
    -moz-animation: fa-spin 1s infinite steps(8);
    animation: fa-spin 1s infinite steps(8);
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -15px;
    margin-top: -15px;
    width: 30px;
    height: 30px;
    text-align: center;
    z-index: 2; }
  .map-wrapper.map-loading:before, .map-wrapper.map-loading.map-style-fullscreen .fullscreen-wrap:before {
    content: '';
    opacity: 0.5;
    background: #ddd;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  .map-wrapper > div,
  .map-wrapper .map-slider,
